import request from '@/static/request.js'
export default {
  data() {
    return {
      userId: 0,
      camps: [],
    }
  },
  onLoad(options) {
    this.id = options.id
    uni.getStorage({
      key: 'GetId',
      success: res => {
      }
    })
  },
  methods: {
    toCamp(a) {
      uni.setStorage({
        key: 'CampId',
        data: a,
      })
      uni.navigateTo({
        url: '../apply/campDetail'
      })
    },

    ifStarted(time) {
      var startEnd = time.split(',')
      var starttime = new Date(Date.parse(startEnd[0]))
      if(new Date() < starttime) {
        return false
      } else {
        return true
      }
    },
    ifGone(time) {
      var startEnd = time.split(',')
      var endtime = new Date(Date.parse(startEnd[1]))
      if(new Date()<endtime) {
        return false
      } else {
        return true
      }
    }
  },
  mounted() {
    const vm = this
    request
      .get('Camp/', {
        
      })
      .then(res => {
        vm.camps = res.data
      })
  },
};
